raul-footer {
  display: block;
  color: #37474F;
  font-size: 0.75rem;
  border-bottom-width: 4px;
  border-color: #0076CC;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

raul-footer .r-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

raul-footer a {
  color: #37474F;
}

raul-footer a:hover {
  text-decoration: underline;
}

raul-footer-contact, raul-footer-copyright, raul-footer-social-icons, raul-footer-navigation-links {
  display: block;
  width: 100%;
  text-align: center;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

raul-footer-contact {
  order: 2;
  margin-bottom: 1rem;
}

raul-footer-copyright {
  order: 3;
  margin-bottom: 0.25rem;
}

raul-footer-social-icons {
  order: 1;
  color: #9BA3A7;
  font-size: 1rem;
  margin-bottom: 0.75rem;
}

raul-footer-social-icons a {
  transition: all .2s ease-in-out;
}

raul-footer-social-icons a:hover {
  color: #0076CC;
}

raul-footer-navigation-links {
  order: 4;
}

raul-footer-social-icon, raul-footer-navigation-link {
  display: inline-block;
}

raul-footer-social-icon {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  vertical-align: middle;
}

raul-footer-social-icon:first-of-type {
  padding-left: 0;
}

raul-footer-social-icon:last-of-type {
  padding-right: 0;
}

raul-footer-navigation-link {
  display: inline-block;
  vertical-align: middle;
  padding-left: 2px;
  padding-right: 2px;
}

raul-footer-navigation-link:first-of-type {
  padding-left: 0;
}

raul-footer-navigation-link:last-of-type {
  padding-right: 0;
}

raul-footer-navigation-link:not(:last-of-type)::after {
  display: inline-block;
  margin-left: 0.25rem;
  content: "|";
}

@media (min-width: 768px) {
  raul-footer {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    align-items: center;
  }

  raul-footer-contact, raul-footer-copyright, raul-footer-social-icons, raul-footer-navigation-links {
    width: 50%;
  }

  raul-footer-contact {
    order: 1;
    text-align: left;
    margin-bottom: 0;
  }

  raul-footer-copyright {
    margin-bottom: 0;
    text-align: left;
  }

  raul-footer-social-icons {
    order: 2;
    text-align: right;
  }

  raul-footer-navigation-links {
    text-align: right;
  }
}

@media (min-width: 1280px) {
  raul-footer-contact, raul-footer-copyright, raul-footer-navigation-links {
    width: 33.333333%;
  }

  raul-footer-contact {
    margin-bottom: 0;
  }

  raul-footer-copyright {
    text-align: center;
  }

  raul-footer-social-icons {
    order: -9999;
    width: 100%;
  }
}